import { Injectable } from '@angular/core';
import { RxError } from '@helix/platform/utils';
import { IViewActionService } from '@helix/platform/view/api';
import { EMPTY, Observable, throwError } from 'rxjs';
import { IApplyGridFilterViewActionParams, RxApplyGridFilterActionName } from './apply-grid-filter-view-action.types';

@Injectable({
  providedIn: 'root'
})
export class RxApplyGridFilterViewActionService implements IViewActionService<IApplyGridFilterViewActionParams, never> {
  execute(params: IApplyGridFilterViewActionParams): Observable<never> {
    if (params.targetApi) {
      if (params.targetApi.applyFilters) {
        params.targetApi.applyFilters(params.filters, params.mode);

        return EMPTY;
      } else {
        throwError(new RxError(`${RxApplyGridFilterActionName}: target component does not support applyFilters API.`));
      }

      return EMPTY;
    } else {
      return throwError(new RxError('rxApplyGridFilterAction: component is not specified.'));
    }
  }
}
